.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: #0009;
  z-index: 1000000; }

.start_bnt {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: 0;
  outline: 0;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 40%; }
  .start_bnt:hover {
    background: rgba(97, 97, 97, 0.363) url(data:image/png;base64,) no-repeat center center;
    background-size: 40%; }

.quick_icon {
  position: absolute;
  left: 10%;
  top: 0; }
  .quick_icon li {
    width: 33px;
    padding: 6px 10px;
    float: left; }
    .quick_icon li img {
      width: 100%; }
    .quick_icon li:hover {
      background-color: #ffffff2e; }
    .quick_icon li.open_icon {
      border-bottom: 2px solid #6b9983; }
    .quick_icon li.active_icon {
      background-color: #ffffff47 !important; }
      .quick_icon li.active_icon:hover {
        background-color: #ffffff5e !important; }
  .quick_icon:after {
    content: '';
    display: block;
    clear: both; }

