div.max_modal {
  width: 100%;
  height: 100%;
  left: 0 !important;
  top: 0  !important;
  -webkit-transform: translate(0px, 0px) !important;
          transform: translate(0px, 0px) !important; }
  div.max_modal .menu-list {
    height: calc(100vh - 38px); }
  div.max_modal .sub-content {
    height: calc(100vh - 38px); }

.modal {
  position: fixed;
  width: 50vw;
  min-width: 450px;
  height: 50vh;
  left: 5%;
  top: 10%;
  background: #fff;
  border: 1px solid #023885;
  box-shadow: 3px 3px 30px #023885;
  opacity: 1 !important; }

.pop_head {
  position: relative;
  height: 38px;
  cursor: move; }
  .pop_headdiv {
    float: left; }
  .pop_head:after {
    display: block;
    content: '';
    clear: both; }
  .pop_head div {
    float: left; }

.pop_folder_img {
  padding: 5px; }
  .pop_folder_img img {
    width: 20px; }

.pop_title {
  width: calc(100% - 140px);
  border-left: 1px solid #777;
  margin: 10px;
  text-align: left;
  padding: 0 10px 0; }

.pop_mng {
  position: absolute;
  width: 100px;
  right: 0;
  top: 0; }
  .pop_mng li {
    float: left;
    width: 33.3%;
    padding: 10% 0;
    cursor: pointer; }
    .pop_mng li input[type='button'] {
      width: 100%;
      border: 0; }
    .pop_mng li:hover {
      background: #ddd; }
      .pop_mng li:hover:last-child {
        background: #ff2e44; }
        .pop_mng li:hover:last-child .pop_close {
          background: url(data:image/gif;base64,R0lGODlhCgAKAJECAP///wAAAP///wAAACH5BAEAAAIALAAAAAAKAAoAAAIRhI5nlsKenprNyXjplS+vqhQAOw==) no-repeat center; }

.after:after {
  clear: both;
  display: block;
  content: ''; }

.pop_minsize {
  background: url(data:image/gif;base64,R0lGODlhCgAKAJECAAAAMwAAAP///wAAACH5BAEAAAIALAAAAAAKAAoAAAILlI+pyxgPY5u0sgIAOw==) no-repeat center; }

input.pop_maxsize_on {
  background: url(data:image/gif;base64,R0lGODlhCgAKAJECAAAAMwAAAP///wAAACH5BAEAAAIALAAAAAAKAAoAAAIUlIKJFuanFHxs0raufbjdRB1MlBUAOw==) no-repeat center; }

input.pop_maxsize {
  background: url(data:image/gif;base64,R0lGODlhCgAKAJECAAAAMwAAAP///wAAACH5BAEAAAIALAAAAAAKAAoAAAIRjI8hy2vdHnRqCjkx1LHmlBQAOw==) no-repeat center; }

.pop_close {
  background: url(data:image/gif;base64,R0lGODlhCgAKAJECAMzMzAAAAP///wAAACH5BAEAAAIALAAAAAAKAAoAAAIWDIQJZil7IJLTUBWae/k4eFHSxZFVAQA7) no-repeat center; }

.pop_preview {
  height: calc(50vh - 38px); }
  .pop_preview > div {
    float: left; }
  .pop_preview::after {
    clear: both;
    display: block;
    content: ''; }
  .pop_preview .menu-list {
    width: 100px;
    height: calc(50vh - 38px);
    padding-right: 4px;
    border-right: 1px solid #ddd; }
    .pop_preview .menu-list li {
      font-size: 0.8rem;
      cursor: pointer;
      padding: 5px 0 5px 15px;
      background: url(data:image/png;base64,) no-repeat 2px 8px;
      background-size: 10%; }
      .pop_preview .menu-list li:hover {
        background-color: #e0f5f9; }
  .pop_preview .sub-content {
    width: calc(100% - 110px);
    height: calc(50vh - 38px);
    margin-left: 5px;
    white-space: nowrap;
    overflow-x: auto; }

