.rightMenu {
  position: fixed;
  top: 0;
  left: 0; }

.contextMenu {
  position: fixed;
  background: #eee;
  box-shadow: 0px 2px 8px #999;
  border: 2px solid #eee;
  z-index: 10000000; }
  .contextMenu--option {
    margin-left: 15px;
    padding: 6px 50px 5px 10px;
    min-width: 100px;
    cursor: default;
    font-size: 12px; }
    .contextMenu--option:hover {
      background: #ddd; }
    .contextMenu--option:active {
      background: #ddd; }
    .contextMenu--option__disabled {
      color: #999;
      pointer-events: none; }
  .contextMenu--separator {
    width: 96%;
    height: 1px;
    background: #999;
    margin: 0;
    margin-left: 2%; }
